import axios from 'axios'
import { makeUseAxios } from 'axios-hooks'
 
export const usePostAxios = makeUseAxios({
  axios: axios.create({ baseURL: 'https://sonicc.herokuapp.com/api/',method: 'POST' })
})
export const useGetAxios=makeUseAxios({
  axios: axios.create({ baseURL:'https://sonicc.herokuapp.com/api/', method: 'GET' })
})

export const ApiGET = (endpoint) => { 
  return new Promise((resolve, reject)=>{ 
    axios({
      method: 'GET',
      url:`https://sonicc.herokuapp.com/api/${endpoint}`,
      data:null
    })
    .then(res=> {resolve(res.data)})
    .catch(err=>{reject(err)});
  })
}