import React from 'react';
import {
    Form,
    Input,
    Upload,
    Button,
    Radio 
  } from 'antd';
  import { UploadOutlined} from '@ant-design/icons';
  import PriceInput from './PriceInput'
  import AmountInput from './AmountInput'

  const { TextArea } = Input;
  const layout = {
    labelCol: {
      span: 6,
    },
    wrapperCol: {
      span: 12,
    },
  };
const normFile = e => {
    console.log('Upload event:', e);
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };
const checkPrice = (rule, value) => {
     if (value.number > 1000) {
        
        return Promise.resolve()
    }
    return Promise.reject('Giá thành sản phẩm phải lớn hơn 1000 VND!');
   
    
  };
  const checkAmount = (rule, value) => {
    if (value.number > 0) {
      return Promise.resolve();
    }
    return Promise.reject('Số lượng sản phẩm phải lớn hơn 0!');
  };
const Formadd=()=>{
    
      
    return (
        
        <div style={{margin: 50,padding: 50, backgroundColor: '#fff' }} className="container">
            <div align="center" >
                <h1 style={{fontSize: 50}}>Thêm sản phẩm</h1>
            </div>
            <Form {...layout}>
      <Form.Item
        hasFeedback
        name="Tên sản phẩm"
        label="Tên sản phẩm"
        rules={[
          
          {
            required: true,
            message: 'Vui lòng điền tên sản phẩm!',
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item 
        name="Loại sản phẩm"
        label="Loại sản phẩm">
        <Radio.Group initalvalue="a" buttonStyle="solid">
        <Radio.Button value="MobliePhone">Điện thoại</Radio.Button>
        <Radio.Button value="Laptop">Laptop</Radio.Button>
        <Radio.Button value="Gear">Phụ kiện</Radio.Button>
        </Radio.Group>
      </Form.Item>
      <Form.Item 
      hasFeedback
      name="Số lượng" 
      label="Số lượng" 
      rules={[
          {
            required: true,
          },
        { validator: checkAmount }]}>
        <AmountInput />
      </Form.Item>

      <Form.Item 
    hasFeedback
      name="Giá tiền một sản phẩm"
        label="Giá tiền một sản phẩm"
        rules={[
          {
            required: true,
          },
          { validator: checkPrice }]}>
        <PriceInput />
      </Form.Item>
      <Form.Item 
      hasFeedback
      name="Thông số chi tiết" 
      label="Thông số chi tiết" 
      rules={[
          {
            required: true,
            message: 'Vui lòng điền thông số chi tiết cho sản phẩm!'
          },
      ]}>
        <TextArea  style={{height: 300}} />
      </Form.Item>
      <Form.Item
        name="Ảnh sản phẩm"
        label="Ảnh sản phẩm"
        valuePropName="fileList"
        getValueFromEvent={normFile}
        rules={[{
            required: true,
        }]}
      >
        <Upload name="logo" action="/upload.do" listType="picture">
          <Button>
            <UploadOutlined /> Click to upload
          </Button>
        </Upload>
      </Form.Item>


      <Form.Item wrapperCol={{ span: 10, offset: 10 }}>
        <Button type="primary" htmlType="submit">
          Thêm sản phẩm
        </Button>
      </Form.Item>
     

    </Form>
        </div>
    )
}
export default Formadd