import React from 'react';
 import {Switch, Route,Redirect} from 'react-router-dom'
 import Login from '../pages/Loginpage/Login'
 import Home from '../pages/Homepage/Home'
 import Addproduct from '../pages/Addproductpage/Addproduct'
 import Laptop from '../components/products/laptop'
 import Phone from '../components/products/phone'
 import Gear from '../components/products/gear'

 const ProtectedRoute = ({ component: Component = null, path, children = null, user = null, isLogin, ...rest }) => {
    console.log('islogin', isLogin)
      if (!isLogin) return <Redirect to={'/login'} />;
    if (children) {
      return (
        <Route path={path} {...rest}>
          {children}
        </Route>
      );
    }
    return <Route path={path} {...rest} render={props => <Component {...props} />} />;
  };
  export  const renderRoutes=(isLogin) =>{
    
     return(
         <Switch>
              <ProtectedRoute exact path="/" render={()=><Home></Home>}  isLogin={isLogin}></ProtectedRoute>
              <Route exact path="/login" render={()=><Login></Login>}> </Route>
              <ProtectedRoute exact path="/addproduct" render={()=><Addproduct></Addproduct>}   isLogin={isLogin}></ProtectedRoute>
              <ProtectedRoute exact path="/laptop" render={()=><Laptop></Laptop>}  isLogin={isLogin}></ProtectedRoute>
              <ProtectedRoute exact path="/phone" render={()=><Phone></Phone>} isLogin={isLogin}></ProtectedRoute>
              <ProtectedRoute exact path="/gear" render={()=><Gear></Gear>}  isLogin={isLogin}></ProtectedRoute>
         </Switch>
     )
 }
