import React, {useEffect,useState,useContext} from 'react';
import './App.less';
import {Link} from 'react-router-dom'
import {List, Card, Button, BackTop, Spin,} from 'antd'
import {Input} from 'antd';
import {UpSquareOutlined} from '@ant-design/icons'
import axios from 'axios'
import ReadMoreAndLess from 'react-read-more-less';
import {AuthContext} from '../../contexts/AuthContext'
import * as ApiClient from '../../helpers/ApiClient'
const { Search } = Input;

const Product=()=>{
  const context=useContext(AuthContext)
  const state = context.state;
  let token=state.token
  const [data,setData]=useState([]);
  const [loading, setLoading]=useState(true)
  axios.interceptors.request.use(
    config=>{
      config.headers.authorization= `Bearer ${token}`
      return config
    },
    error=>{
      return Promise.reject(error)
    }
  )
  const fetchData=async ()=>{
    const data= await ApiClient.ApiGET('/products/?category=phone');
    setData(data)
    console.log('thanh', data)
    
    setLoading(false)
    
  }
  useEffect(()=>{

    fetchData()
    
    // console.log(data)
  },[])
if(loading){
  return(
          <div align="center" className="site-layout-background" style={{ padding: 24, minHeight: 360,  }}>
        
    
        <Spin style={{marginTop: 200,alignContent: 'center', justifyContent: 'center', alignItems: 'center'}} size="large" />
        
      </div>
  )
}
    return (
          
          
            <div  className="site-layout-background" style={{ padding: 24, minHeight: 360,  }}>
              <div style={{margin: 20,}}>
                <Link to='/addproduct'>
              <Button type="primary" shape="round"  >
                 Thêm sản phẩm
               </Button>
               </Link>
               <Search size="large"   style={{marginLeft: 138,width: 600}} placeholder="input search text" enterButton />
               
              </div>
  <List
    
    grid={{ gutter: 16, column: 2 }}
    dataSource={data}
    pagination={{
      onChange: page => {
        console.log(page);
      },
      pageSize: 4,
    }}
    renderItem={item => (
      
      <List.Item key={item.id}>
        <Card title='Tên sản phẩm'>{item.name}</Card>
        <Card title='Giới thiệu'>
        <ReadMoreAndLess
        // ref={this.ReadMore}
        className="read-more-content"
        charLimit={250}
        readMoreText="Hiển thị thêm"
        readLessText="Ẩn"
        >{item.description1+item.description2+item.description3}
        </ReadMoreAndLess>
        </Card>
        <Card  title='Hình ảnh'>{[
          <img
          style={{margin: 40 }}
          
          width={200}
          height ={180}
          alt="logo"
          src={item.image}
        />
      
        ]}</Card>
          <Card title='Sản phẩm còn lại'><b style={{color: 'coral'}}>{item.amount} sản phẩm</b></Card>
          <Card title='Giá tiền'><b style={{color: 'red'}}>{item.price} VND</b></Card>
          <div>
            <Button style={{width: 250, borderRadius: 25, margin: 20}} type='primary'>Sửa thông tin sản phẩm</Button>
            <Button style={{width: 250, borderRadius: 25, margin: 20}} danger>Xoá sản phẩm</Button>
          </div>
      </List.Item>
     
      
    )}
  />,

             <BackTop>
           <div style={{height: 40,
                width: 50,
                lineHeight: '40px',
                borderRadius: 25,
                backgroundColor: '#1088e9',
                color: '#fff',
                textAlign: 'center',
                fontSize: 14,}}> {<UpSquareOutlined />}</div>
               
          </BackTop>
            </div>
         
    )
}


export default Product