import React,{useContext} from 'react';
import './App.less';
import {Layout} from 'antd'
 
import {renderRoutes} from '../routes/index'


import Headerbar from '../layout/Headerbar'

import Sidebar from '../layout/Sidebar'
import {AuthContext} from '../contexts/AuthContext'
import {AppContext} from '../contexts/AppContext'
const {Content, Footer}=Layout;






function App() {
let isLogin=null;
const context=useContext(AuthContext);
const state=context.state;

// const token=localStorage.getItem('_token')
console.log("stateeeeeeeeee",state)
if(state.token){
  isLogin=true;
}else{
  isLogin=false;
}
console.log('login',isLogin)
  return (
    <AppContext.Consumer>
      {(onUpdateContext)=>{
       if (isLogin=== true) {
            return(
              <Layout >
       <Headerbar style={{position: 'fixed'}}></Headerbar>
        <Layout >
        <Sidebar></Sidebar>
        <Content style={{marginLeft: 200, marginTop: 50}}>{renderRoutes(isLogin)}</Content>
      </Layout>
     <Layout>
      <div style={{backgroundColor:'#2dfa2d' }}>
     
       <Footer  style={{ textAlign: 'center' }}>Powered by Nguyen Hop Thanh</Footer></div>
     </Layout>
     </Layout>
            )
        } 
        if(isLogin=== false){
            return(
              <Content >{renderRoutes(isLogin)}</Content>
            )
        }
      }
      }
  </AppContext.Consumer>
 
) 
}
export default App;
