import React, {useContext, useState} from 'react';
import '../../assets/less/App.less';

import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { Form, Input, Button, Checkbox } from 'antd';
import '../../assets/less/App.less'
import '../../assets/less/Login.less'
import loinImage from '../../assets/images/login.jpeg';
import { AuthContext} from '../../contexts/AuthContext';
import history from '../../services/history'
import {usePostAxios} from '../../helpers/ApiClient'

const Login=()=>{
  const authContext = useContext(AuthContext);
  // const {state} = React.useContext(AuthContext)
  const [username, setUsername]=useState('');
  const [password, setPassword]=useState('');

  const [ 
    { data},
    login
  ] = usePostAxios('/user/login')
  console.log('data', data)
  if(data){
    localStorage.setItem('_refreshToken',data.refreshToken);
    localStorage.setItem('_token',data.idToken);
    localStorage.setItem('_expiresAt', data.expiresAt)
    authContext.actions.setToken(data.idToken);
    history.push('/')
  }
  const hanldeLogin=()=>{
    login({
      data: {
        username: username,
        password: password
      }
    })
    
    
    
  }
    return(     
          <div className="base-container" >
          <div className="header">Login</div>
          <div className="content">
            <div className="image">
              <img src={loinImage} alt='' />
            </div>
            <Form
          name="normal_login"
          className="login-form"
          initialValues={{ remember: true }}
        >
          <Form.Item
            name="username"
            rules={[{ required: true, message: 'Vui lòng điền Username!' }]}
          >
            <Input 
            style={{ color: 'rgba(0,0,0,.25)'}}
            prefix={<UserOutlined 
            className="site-form-item-icon" />}
             placeholder="Username" 
             onChange={(e)=>{setUsername(e.target.value)}}/>
          </Form.Item>
          <Form.Item
            name="password"
            rules={[{ required: true, message: 'Vui lòng điền Password!' }]}
          >
            <Input.Password
              style={{ color: 'rgba(0,0,0,.25)'}}
              prefix={<LockOutlined className="site-form-item-icon" />}
              type="password"
              placeholder="Password"
              onChange={(e)=>{setPassword(e.target.value)}}
            />
          </Form.Item>
          <Form.Item>
            <Form.Item name="remember" valuePropName="checked" noStyle>
              <Checkbox>Remember me</Checkbox>
            </Form.Item>
          </Form.Item>
    
          <Form.Item>
            
            <Button type="primary" htmlType="submit" className="login-form-button" onClick={hanldeLogin}> 
              Log in
            </Button>
          </Form.Item>
          
        </Form>
          </div>
          
        </div>
    
           
        
       
    )
}
export default Login